import axios from 'axios'

// Create instance called instance
const instance = axios.create({
  baseURL: process.env.VUE_APP_BACKEND || `http://localhost:8081`,
  headers: {},
})
export default {
  getPhotoList: () =>
    instance({
      method: 'GET',
      url: '/photolistold/',
    }),
  getPhotoBaseUrl: () => {
    return instance.defaults.baseURL + "/photos/"
  },
}